body {
  margin: 0; }

stat-block {
  /* A bit of margin for presentation purposes, to show off the drop
  shadow. */
  margin-left: 20px;
  margin-top: 20px; }

svg {
  fill: #922610;
  /* Stroke is necessary for good antialiasing in Chrome. */
  stroke: #922610;
  margin-top: 0.6em;
  margin-bottom: 0.35em; }

.bar {
  height: 10px;
  background: #E69A28;
  border: 1px solid #000;
  position: relative;
  z-index: 1; }

.content-wrapper {
  font-family: 'Noto Sans', 'Myriad Pro', Calibri, Helvetica, Arial, sans-serif;
  font-size: 13.5px;
  background: #FDF1DC;
  padding: 0.6em;
  padding-bottom: 0.5em;
  border: 1px #DDD solid;
  box-shadow: 0 0 1.5em #867453;
  /* We don't want the box-shadow in front of the bar divs. */
  position: relative;
  z-index: 0;
  /* Leaving room for the two bars to protrude outwards */
  margin-left: 2px;
  margin-right: 2px;
  /* This is possibly overriden by next CSS rule. */
  -webkit-column-gap: 40px;
  -moz-column-gap: 40px;
  column-gap: 40px;
  /* When height is constrained, we want sequential filling of columns. */
  -webkit-column-fill: auto;
  -moz-column-fill: auto;
  column-fill: auto; }

.panel-heading .edit-btn {
  float: right;
  font-size: 150%;
  margin-top: -4px;
  cursor: pointer; }

.panel-body h1 {
  font-family: 'Libre Baskerville', 'Lora', 'Calisto MT', 'Bookman Old Style', Bookman, 'Goudy Old Style', Garamond, 'Hoefler Text', 'Bitstream Charter', Georgia, serif;
  color: #7A200D;
  font-weight: 700;
  margin: 0px;
  font-size: 23px;
  letter-spacing: 1px;
  font-variant: small-caps; }

.panel-body h2 {
  font-weight: normal;
  font-style: italic;
  font-size: 12px;
  margin: 0; }

.panel-body .stat-set {
  float: left;
  padding-left: 14px;
  padding-right: 14px; }
  .panel-body .stat-set .stat-key {
    color: #7A200D;
    padding-bottom: 4px;
    text-align: center; }
    .panel-body .stat-set .stat-key h4 {
      font-weight: bold;
      font-size: 13.5px;
      margin: 0; }
